<template>
<div class="s-col00">
	<div class="m-lg">
		<div class="g-lg">
			<div class="m-lg-nav">
				<img src="../../static/images/lg_logo.png" class="u-logo">
				<ul class="r">
					<li class="on"></li>
					<li></li>
					<li></li>
				</ul>
			</div>
			<img src="../../static/images/lg_against.png" class="u-dzt">
			<img src="../../static/images/lg_jb.png" class="u-jb">
			<div class="m-lg-box">
				<p class="u-p-lg"></p>
				<form class="mt56">
					<div class="g-ipt-lg"><input type="text" name="telephone" placeholder="/" class="u-ipt-lg" v-model="username"></div>
					<div class="g-ipt-lg"><input type="password" name="password" placeholder="" class="u-ipt-lg" v-model="password" @keyup.enter="checkin"></div>
					<div class="g-forget" style="display:none;">
						<label class="l re">
							<input type="checkbox" name="">
							<i class="u-mind-cb"></i>
						</label>
						<span class="r"></span>
					</div>
					<div class="tips" style="margin-right:80px;">
						<div class="attention"></div>
					</div>
				</form>
				<button type="button" class="u-lg-btn" @click = "checkin">&nbsp&nbsp</button>
			</div>
			<ul class="g-ts clearfix">
				<li>
					<img src="../../static/images/lg_ts1.png">
					<p class="s-ts-tit"></p>
					<p class="mt10"></p>
					<p class="mt2"></p>
				</li>
				<li>
					<img src="../../static/images/lg_ts4.png">
					<p class="s-ts-tit"></p>
					<p class="mt10"></p>
					<p class="mt2"></p>
				</li>
				<li>
					<img src="../../static/images/lg_ts3.png">
					<p class="s-ts-tit"></p>
					<p class="mt10"></p>
					<p class="mt2"></p>
				</li>
				<li>
					<img src="../../static/images/lg_ts2.png">
					<p class="s-ts-tit"></p>
					<p class="mt10"></p>
					<p class="mt2"></p>
				</li>
			</ul>
		</div>
	</div>
</div>
</template>
<script type="text/javascript">
 
  export default {
    data () {
      return {
        username:'',
        password:''
      }
  },
  methods: {
  	checkin:function(){
  		var username = $('[name="telephone"]').val();
  		var pwd = $('[name="password"]').val();
  		if(username=='' && pwd==''){
  			$('.attention').text('!').show();
  		}
  		else if(username==''){
  			$('.attention').text('!').show();
  		}
  		else if(pwd==''){
			$('.attention').text('!').show();
  		}
  		else{
  			this.$http.post('event/login', this.$data).then(function (response) {
  				var code = response.data.code;
  				if(code==-10){
  					$('.attention').text('!').show();
  				}
  				else{
  					var msg = response.data.msg;
  					if(msg==''){
  						$('.attention').hide();
  					}
  					else{
  						$('.attention').text(msg).show();
  					}
  					document.cookie="oetevent.login.sessionid="+response.data.object["oetevent.login.sessionid"];
			  		document.cookie="oetevent.login.token="+response.data.object["oetevent.login.token"];
			  		document.cookie="userId="+response.data.object.userInfo["id"];
			  		window.sessionStorage.setItem("username",response.data.object.userInfo["nickname"]);
			  		this.$route.router.go({path: '/homepage'}); 
  				}
	            }, function (response) {
	              	console.log(22);
	            }) 
  		}
  		
  	}
  }
  }
</script>


