<template>
	<top-head></top-head> 
	 <top-nav></top-nav> 
	<div class="g-mgbg">
		<div class="g-mg-reset">
			<div class="g-tp"></div>
			<div class="m-d">
				<p class="m-d-tit">{{eventname}}</p>
				<div class="s-d">
					<div class="s-d-in">
						<p class="mb12 ls1"></p>
						<p class="s-del"></p>
						<button type="button" class="u-del-btn" @click="resetEvent">&nbsp&nbsp</button>
					</div>
				</div>
				<div class="s-d">
					<div class="s-d-in">
						<p class="mb12 ls1"></p>
						<p class="s-del"></p>
						<button type="button" class="u-del-btn" @click="deleteEvent">&nbsp&nbsp</button>
					</div>
				</div>
			</div>
			<div class="g-bt"></div>	
		</div>
		<div class="footer footer-del">
			<p>Copyright  2016 </p>
			<p class="mt6">ICP13005497-5</p>
			<img src="../../static/images/footer.png" class="mt32">
		</div>
	</div>
</template>
<script>
import topNav from '../components/topNav.vue'
import topHead from '../components/topHead.vue'
  export default {
    data () {
      return {
      	eventname:"",
        oetInfoId:"",
        oetRoundId:""
      }
  	},
  	ready:function(){
  		var _this=this;
  		_this.eventname=window.sessionStorage.getItem("eventname");
  		_this.oetInfoId=window.sessionStorage.getItem("eventid");
  		_this.oetRoundId=window.sessionStorage.getItem("roundid");
  	},
  	methods:{
  		resetEvent:function(e){
  			var _this=this;
  			layer.confirm('', {
			  	btn: ['',''], //
			  	move:false
			}, function(){
			  	var parm={};
	   			parm.jsonInfo=JSON.stringify({oetRoundId:_this.oetRoundId});
				_this.$http.get('event/reset',parm).then(function(response) {
		        	console.log(response.data);
		        	if(response.data.code==1){
		        		layer.msg("",{offset:"0px"});
		        	}else{
		        		layer.msg(response.data.msg,{offset:"0px"});
		        	}		        	
		        },function(response) {
		            console.log(response);
		        });
			}, function(){
			  	
			});
  		},
  		deleteEvent:function(e){
  			var _this=this;
  			layer.confirm('', {
			  	btn: ['',''], //
			  	move:false
			}, function(){
			  	var parm={};
	   			parm.jsonInfo=JSON.stringify({oetInfoId:_this.oetInfoId});
				_this.$http.get('event/delete',parm).then(function(response) {
		        	console.log(response.data);
		        	if(response.data.code==1){
		        		layer.msg("",{offset:"0px"});
		        		_this.$route.router.go({path: '/homepage'});
		        	}else{
		        		layer.msg(response.data.msg,{offset:"0px"});
		        	}		        	
		        },function(response) {
		            console.log(response);
		        });
			}, function(){
			  	
			});
  		}
  	},
    components: {
          topNav,
          topHead
         
  }
  }
</script>


